// SPINNER--------------------------------------------------------
export default function spinner() {
	const loadingClass = document.querySelectorAll('.loading');  //   '.loading'
	loadingClass.forEach(item => { //     '.loading'        showSpinner()
		showSpinner(item); //   item  loadingClass == '.loading'
	});
	function showSpinner(loadingClass) { //   showSpinner()   loadingClass == '.loading'
		const spinnerMessage = {loading: 'img/form/spinner.svg'}; //  spinnerMessage        
		const spinnerImg = document.createElement('img'); //   html   img  span.loading{}
		spinnerImg.src = spinnerMessage.loading; //    
		spinnerImg.style.cssText = `
		display: block;
		margin: 0 auto;
		height: 38px;
		width: 38px;
	`;
		loadingClass.insertAdjacentElement('beforeEnd', spinnerImg); //       insertAdjacentElement(),   - beforeEnd ( ),  -  !!!
	}
}