import {openModalWindow, closeModalWindow} from './modal.js';
import {postData} from '../services/services.js';

// SEND-FORMS---------fetch()        
export default function forms(formSelector, modalTimerId) { // forms('form', modalTimerId)
	const forms = document.querySelectorAll(formSelector); //   'form'
	const message = { //   ,      
		// loading: '...', //  ,      
		loading: 'img/form/spinner.svg', //        div ...
		success: '!    !',
		failure: '-   ...',
	};
	forms.forEach(item => { //    'form'        bindpostData()
		bindPostData(item); //   item  form == 'form'
	});
	function bindPostData(form) { //    (bind)  - ,        submit, =>
		form.addEventListener('submit', (e) => { //        
			e.preventDefault(); //         
			// const statusMessage = document.createElement('div'); //    
			const statusMessage = document.createElement('img'); //   'div'     
			// statusMessage.classList.add('status'); //    
			statusMessage.src = message.loading; //    
			// statusMessage.textContent = message.loading; //     '...'
			statusMessage.style.cssText = `
				display: block;
				margin: 0 auto;
			`; //   
			// form.append(statusMessage); //      '...'
			form.insertAdjacentElement('afterend', statusMessage); //       insertAdjacentElement(),   - afterend ( ),  -  !!!
			const formData = new FormData(form); // new FormData(form) -         ,        ,  !!!
			// FormData(form)   html  name   input  ,     !!!

			// postData('http://localhost:3000/requests', JSON.stringify(objectJson)) //  json   JSON    =>
			//      objectJson,         Json =>  formData         formData.entries(), =>
			const json = JSON.stringify(Object.fromEntries(formData.entries())); //  -    Object.fromEntries(formData.entries(),  ,    JSON    JSON.stringify(Object.fromEntries(formData.entries()))			
			postData('http://localhost:5000/requests', json) //  entries()        formData{},  fromEntries()    
			// .then(data => data.text()) //     ,    postData       
				.then(data => { //    data,    JSON
					console.log(data); //  data ,     PROMISE ( )
					showThanksModal(message.success); //  statusMessage.textContent      showThanksModal()!!!
					statusMessage.remove(); //      PROMISE
				}).catch(() => { // catch       !!!
					showThanksModal(message.failure);//  statusMessage.textContent      showThanksModal()!!!				
				}).finally(() => { // finally        
					form.reset(); //     				
				});
		}); 
	}
	function showThanksModal(message) { //   showThanksModal()         message
		const prevModalDialog = document.querySelector('.modal__dialog'); //   'modal__dialog',   
		prevModalDialog.classList.add('hide'); //   hide  'modal__dialog'
		openModalWindow('.modal', modalTimerId); //    
		const thanksModal = document.createElement('div'); //   'div' -  
		thanksModal.classList.add('modal__dialog'); //    'modal__dialog'    
		thanksModal.innerHTML = ` 
			<div class="modal__content">
				<div class="modal__close" data-close>&times;</div>
				<div class="modal__title">${message}</div>
			</div>
		`; //        (MODAL---- modalWindow.addEventListener('click', (e) => {})   !!!
		document.querySelector('.modal').append(thanksModal); //   '.modal'     thanksModal
		setTimeout(() => { //       4 .      'modal__dialog',    setTimeout()
			thanksModal.remove(); // thanksModal  ,      
			prevModalDialog.classList.add('show'); //       'modal__dialog'
			prevModalDialog.classList.remove('hide');
			closeModalWindow('.modal'); //   ,    
		}, 4000);
	}
}